* {
  box-sizing: border-box;
}

body {
  display: block;
}

.game-box {
  width: 400px;
  height: 400px;
  text-align: center;
  border: 2px solid #00f;
  flex-wrap: wrap;
  margin: 50px auto;
  font-family: sans-serif;
  font-size: 102px;
  display: flex;
}

.cell {
  width: 132px;
  height: 132px;
  cursor: pointer;
  border: 2px solid gold;
  margin: 0;
}

.basicLightbox--visible {
  width: 100%;
  height: 100%;
  background-color: #437485;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.basicLightbox__placeholder {
  width: 100%;
  height: auto;
  text-align: center;
  background: linear-gradient(123deg, #032f3e 15%, #021c25 44%);
  padding: 15px;
}

.message-title {
  color: #d4dd9e;
}

